import { INPUT_DATA_ACTION_TYPES } from "./input-data.actions";

const init_state = {
    clear_span:"6.1",
    support_width:"0.23",
    fck:"25",
    fy:"500",
    d:"250",
    b:"1000",
    effective_cover:"20"
}

export const inputDataReducer = (state=init_state, action)=>{
    const {type, payload}=action;
    switch(type){
        case INPUT_DATA_ACTION_TYPES.SET_INPUT_DATA_FIELD:{
            return({
                ...state,
                ...payload
            })
        }
        default:{
            return({
                ...state
            })
        }
    }
}